.mainContainer {
  max-width: 1440px;
  width: 100%;
  height: 100%;
  margin: auto;
  display: flex;
  flex-direction: row;
}

.imageSection {
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.imageSection .image {
  margin: 10%;
}

.contentSection {
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
  margin: 5% 15% 5% 5%;
}
.contentSection .title {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 42px;
  line-height: 120%;
  color: rgb(18, 18, 18);
}
.contentSection .description {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  color: rgb(18, 18, 18);
  margin-bottom: 50px;
}
.contentSection .button {
  font-family: "Roboto";
  font-style: normal;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 16px 40px;
  width: 128px;
  height: 48px;
  background-color: rgb(228, 193, 174);
  border-radius: 32px;
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
  flex: none;
  order: 0;
  flex-grow: 0;
  color: rgb(255, 255, 255);
  cursor: pointer;
}

.blured {
  display: none;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(18, 18, 18, 0.5);
  z-index: 10;
}
.blured .popup {
  background-color: rgb(255, 255, 255);
  width: 60%;
  height: 250px;
  margin: auto;
  display: flex;
  flex-direction: column;
}
.blured .popup .close {
  width: 100%;
  height: 20%;
  display: flex;
  justify-content: flex-end;
}
.blured .popup .close .closeButton {
  padding: 10px;
  width: 24px;
  height: 24px;
  display: flex;
}
.blured .popup .close .closeButton .x-icon {
  font-size: 30px;
  font-weight: 400;
  display: inline-block;
  transform: rotate(45deg);
  cursor: pointer;
}
.blured .popup .popUpContent {
  width: 100%;
  height: 80%;
}
.blured .popup .popUpContent .title,
.blured .popup .popUpContent .description {
  margin-left: 10%;
}
.blured .popup .popUpContent .button {
  margin-left: 10%;
  font-family: "Roboto";
  font-style: normal;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 16px 40px;
  width: 128px;
  height: 48px;
  background-color: rgb(228, 193, 174);
  border-radius: 32px;
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
  flex: none;
  order: 0;
  flex-grow: 0;
  color: rgb(255, 255, 255);
  cursor: pointer;
}

@media screen and (max-width: 1024px) {
  .contentSection {
    margin: 2% 5%;
  }
}
@media screen and (max-width: 768px) {
  .mainContainer {
    flex-direction: column;
  }
  .mainContainer .imageSection,
.mainContainer .contentSection {
    width: 80%;
    margin: 0 10%;
    justify-content: center;
  }
  .mainContainer .imageSection .title,
.mainContainer .contentSection .title {
    text-align: center;
  }
  .mainContainer .imageSection .description,
.mainContainer .contentSection .description {
    text-align: justify;
  }
  .mainContainer .imageSection .image,
.mainContainer .contentSection .image {
    margin: 10% 0;
  }
  .blured .popup {
    width: 90%;
  }
}